<template>
  <div class="login">
    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="用户名"
        label="用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="密码"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
      <div style="margin: 16px;">
        <van-button 
        round block type="info" 
        native-type="submit">登入</van-button>
      </div>
    </van-form>
    <van-button 
    class="reg"
    color="linear-gradient(to right, #4bb0ff, #6149f6)"
    round
    @click="SkipRegist"
    >
    点击注册
    </van-button>
  </div>
</template>

<script>
import { Form, Field, Button } from "vant";
export default {
  name: "login",
  components: {
    [Form.name]: Form,
    [Field.name]: Field,
    [Button.name]: Button
  },
  data: function() {
    return {
      username: "",
      password: "",
    };
  },
  methods: {
    onSubmit() {
      let data = {
        username: this.username,
        password: this.password
      };
      this.$http.login(data).then(res => {
        console.log("登入成功", res);
        localStorage.setItem("token", res.token);
        this.$router.replace("/s");
      })
    },
    SkipRegist(){
        this.$router.replace("/Regist")
      }
  }
};
</script>
  
<style lang="scss" scoped>
  .login{
    .reg{
      float:right;
      margin-right:.5rem;
    }
  }
</style>